import React, {Component} from 'react';
import {renderToStaticMarkup} from 'react-dom/server';

class Output extends Component {
  componentDidMount() {
    this.renderInnerFrame();
  }

  shouldComponentUpdate(nextProps) {
    const {css, html, javascript, loadType} = nextProps;
    return (
      this.props.css !== css ||
      this.props.html !== html ||
      this.props.javascript !== javascript ||
      this.props.loadType !== loadType
    );
  }

  componentDidUpdate() {
    this.renderInnerFrame();
  }

  saveFrameRef = frame => {
    if (frame) {
      this._iframe = frame;
      this._iframe.addEventListener('load', () => {
        this.renderInnerFrame();
      });
    }
  };

  renderFrameContent(loadType) {
    switch (loadType) {
      case 'onDomReady':
        return (
          <html>
            <head>
              <style dangerouslySetInnerHTML={{__html: this.props.css}} />
              <script
                dangerouslySetInnerHTML={{
                  __html: `
                    var onDomReady = function() {
                      ${this.props.javascript}
                    }
                    document.addEventListener("DOMContentLoaded", function(){
                      onDomReady();
                    });
                  `,
                }}
              />
            </head>
            <body>
              <div dangerouslySetInnerHTML={{__html: this.props.html}} />
            </body>
          </html>
        );
      case 'onLoad':
        return (
          <html>
            <head>
              <style dangerouslySetInnerHTML={{__html: this.props.css}} />
              <script
                dangerouslySetInnerHTML={{
                  __html: `
                    window.onload=function(){
                      ${this.props.javascript}
                    }
                  `,
                }}
              />
            </head>
            <body>
              <div dangerouslySetInnerHTML={{__html: this.props.html}} />
            </body>
          </html>
        );
      case 'head':
        return (
          <html>
            <head>
              <style dangerouslySetInnerHTML={{__html: this.props.css}} />
              <script
                dangerouslySetInnerHTML={{__html: this.props.javascript}}
              />
            </head>
            <body>
              <div dangerouslySetInnerHTML={{__html: this.props.html}} />
            </body>
          </html>
        );
      case 'body':
      default:
        return (
          <html>
            <head>
              <style dangerouslySetInnerHTML={{__html: this.props.css}} />
            </head>
            <body>
              <div dangerouslySetInnerHTML={{__html: this.props.html}} />
              <script
                dangerouslySetInnerHTML={{__html: this.props.javascript}}
              />
            </body>
          </html>
        );
    }
  }

  renderInnerFrame() {
    const html = renderToStaticMarkup(this.renderFrameContent(this.props.loadType));
    this._iframe.contentWindow.postMessage(JSON.stringify({html}), '*');
  }

  render() {
    return (
      <section className="Demo">
        <iframe
          title="Stribble Frame"
          ref={this.saveFrameRef}
          frameBorder="0"
          src="/iframe.html"
        />
      </section>
    );
  }
}

export default Output;



