const transpile = code => {
  let transpiled;
  try {
    transpiled = window.Babel.transform(code, {
      presets: ['es2015', 'react', 'stage-2'],
    });
    return {
      code: transpiled.code,
      error: null,
    };
  } catch (e) {
    return {
      code: '',
      error: e.message,
    };
  }
};

export default transpile;



