import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

import Promise from 'promise-polyfill';
if (!window.Promise) {
  window.Promise = Promise;
}

import 'whatwg-fetch'; // eslint-disable-line import/first

// Add some dumb URL parsing:
const parts = window.location.pathname.replace('/stribble/', '/').split('/');
let result = false;
let id;
if (parts.length > 1) {
  id = parts[1];
  result = parts[2] === 'result';
}

ReactDOM.render(
  <App id={id} resultOnly={result} />,
  document.getElementById('root')
);
registerServiceWorker();



