import React, {Component} from 'react';
import TimeAgo from 'react-timeago';
import classNames from 'classnames';
import './App.css';
import optionsIcon from './icons/options.svg';
import transpile from './lib/transpile';
import MonacoEditor from 'react-monaco-editor';
import NotFound from './NotFound';

import {getStribble, updateStribble, createStribble, forkStribble} from './api';

import Output from './Output';
const Mousetrap = require('mousetrap');

const log = window.console ? window.console.log : () => {};

const HTML_DEFAULT = `<h1>Hello Stribble</h1>
<!-- Press ⌘ ⏎ to run code, ⌘ S to save. -->
`;
const JS_DEFAULT = `/**
  * Write your JavaScript here.
  * Press ⌘ ⏎ to run code, ⌘ S to save.
  */
`;
const CSS_DEFAULT = `/**
  * Write your CSS here.
  * Press ⌘ ⏎ to run code, ⌘ S to save.
  */
`;
const SAVE_COMMAND = ['command+s', 'ctrl+s'];

const MONACO_OPTIONS = {
  selectOnLineNumbers: true,
  fontSize: 12,
  lineHeight: 16,
  wordWrap: 'on',
  renderLineHighlight: 'none',
  // We can handle layout changes manually in case this negatively impacts performance.
  automaticLayout: true,
};

const timeAgoFormatter = (value, unit, suffix, date, defaultFormatter) => {
  if (value < 15 && unit === 'second') {
    return 'just now';
  } else if (value < 60 && unit === 'second') {
    return 'less than a minute ago';
  }
  return defaultFormatter(value, unit, suffix, date);
};

class App extends Component {
  constructor(props) {
    super(props);

    // TODO(michelle): implement this.props.resultOnly.

    const initialState = {
      id: this.props.id,
      name: '',
      updated_at: null,
      jsError: null,
      resizing: false,
      codeContainerWidth: 40,
      jsContainerHeight: 33.3,
      cssContainerHeight: 33.3,
      savedLastRun: true,
      showJsSettings: false,
      jsLoadType: 'body',
    };
    if (this.props.id) {
      getStribble(this.props.id).then(response => {
        if (response.error && response.status === 404) {
          this.setState({
            error: response.error,
          });
        } else {
          const {javascript, css, html, read_only, name, updated_at} = response;
          const {error: jsError, code: transpiledJsCode} = transpile(
            javascript
          );
          this.setState(
            {
              name,
              updated_at,
              jsCode: javascript,
              transpiledJsCode,
              htmlCode: html,
              cssCode: css,
              readOnly: read_only,
              jsError,
            },
            this.handleRun
          );
        }
      });
      this.state = initialState;
    } else {
      this.state = {
        jsCode: JS_DEFAULT,
        transpiledJsCode: JS_DEFAULT,
        htmlCode: HTML_DEFAULT,
        cssCode: CSS_DEFAULT,
        runHtmlCode: HTML_DEFAULT,
        runCssCode: CSS_DEFAULT,

        ...initialState,
      };
    }
  }

  componentWillMount() {
    Mousetrap.bind(SAVE_COMMAND, () => {
      this.handleSave();
      return false;
    });
  }

  componentWillUnmount() {
    Mousetrap.unbind(SAVE_COMMAND);
  }

  componentWillUpdate(nextProps, nextState) {
    if (nextState.id !== this.state.id) {
      window.history.pushState(null, null, `/${nextState.id}`);
    }
  }

  handleEditorMount = type => (editor, monaco) => {
    monaco.editor.defineTheme('stribble', {
      base: 'vs', // can also be vs-dark or hc-black
      inherit: true, // can also be false to completely replace the builtin rules
      rules: [],
    });
    editor.addCommand(
      monaco.KeyMod.CtrlCmd | monaco.KeyCode.KEY_S,
      this.handleSave
    );
    editor.addCommand(
      monaco.KeyMod.CtrlCmd | monaco.KeyCode.Enter,
      this.handleRunClick
    );
    editor.getModel().updateOptions({
      tabSize: 2,
    });
  };

  getValues = () => {
    return {
      name: this.state.name,
      javascript: this.state.jsCode,
      css: this.state.cssCode,
      html: this.state.htmlCode,
    };
  };

  handleSave = () => {
    // Also run the code!
    this.handleRun();
    this.setState({savedLastRun: true});

    // TODO(stuart): if the user is on a slow connection,
    // any changes in between save & actual save completion
    // will be lost.

    const data = this.getValues();
    if (this.state.id) {
      updateStribble(this.state.id, data).then(res => {
        log(`Updated ${this.state.id}:`, res);
        this.setState({
          id: res.slug,
          name: res.name,
          updated_at: res.updated_at,
        });
      });
    } else {
      createStribble(data).then(res => {
        log(`Created ${res.slug}`);
        this.setState({id: res.slug, updated_at: res.updated_at});
        window.history.pushState(null, null, `/${res.slug}`);
      });
    }
  };

  handleInputChange = key => ev => {
    this.setState({[key]: ev.target.value});
  };
  handleChange = key => val => {
    this.setState({[key]: val});
  };

  handleRunClick = () => {
    this.setState({
      savedLastRun: false,
    });
    this.handleRun();
  };

  handleRun = () => {
    const {
      javascript,
      html: runHtmlCode,
      css: runCssCode,
      name,
    } = this.getValues();

    const {error, code: transpiledJsCode} = transpile(javascript);
    this.setState({
      jsError: error,
      name,

      // Save the code to run
      transpiledJsCode,
      runHtmlCode,
      runCssCode,
    });
  };
  handleFork = () => {
    if (!this.state.id) {
      throw new Error("Cannot for a stribble that hasn't been saved");
    }

    forkStribble(this.state.id).then(res => {
      log(`Forked ${this.state.id}:`, res);
      if (this.props.resultOnly) {
        window.location.assign(`/${res.slug}`);
        return;
      }
      this.setState({id: res.slug}, () => {
        log(`Now using ID ${this.state.id}`);
        this.handleSave();
      });
    });
  };

  handleHorizontalResize = e => {
    const curPos = e.pageX - this.offsetX;
    const {width} = this.codeContainer.getBoundingClientRect();
    const codeContainerWidth = curPos / width * 100;
    this.setState({codeContainerWidth: codeContainerWidth});
  };
  handleJsResize = e => {
    const curPos = e.pageY - this.offsetY;
    const {height, top} = this.codeContainer.getBoundingClientRect();
    const combinedHeight =
      this.state.cssContainerHeight + this.state.jsContainerHeight;
    let jsContainerHeight = (curPos - top) / height * 100;
    if (jsContainerHeight < 10) {
      jsContainerHeight = 10;
    }
    if (jsContainerHeight > combinedHeight - 10) {
      jsContainerHeight = combinedHeight - 10;
    }
    const cssContainerHeight = combinedHeight - jsContainerHeight;
    this.setState({jsContainerHeight, cssContainerHeight});
  };
  handleCssResize = e => {
    const curPos = e.pageY - this.offsetY;
    const {height, top} = this.codeContainer.getBoundingClientRect();
    const htmlContainerHeight =
      100 - this.state.jsContainerHeight - this.state.cssContainerHeight;
    const combinedHeight = htmlContainerHeight + this.state.cssContainerHeight;
    let cssContainerHeight =
      (curPos - top) / height * 100 - this.state.jsContainerHeight;
    if (cssContainerHeight < 10) {
      cssContainerHeight = 10;
    }
    if (cssContainerHeight > combinedHeight - 10) {
      cssContainerHeight = combinedHeight - 10;
    }
    // if (cssContainerHeight + this.state.jsContainerHeight > 90) {
    //   cssContainerHeight = 100 - this.state.jsContainerHeight - 10;
    // }
    this.setState({cssContainerHeight});
  };

  handleHorizontalResizeEnd = e => {
    this.setState({resizing: false});
    document.removeEventListener('mousemove', this.handleHorizontalResize);
    document.removeEventListener('mouseup', this.handleHorizontalResizeEnd);
  };
  handleJsResizeEnd = e => {
    document.removeEventListener('mousemove', this.handleJsResize);
    document.removeEventListener('mouseup', this.handleJsResizeEnd);
  };
  handleCssResizeEnd = e => {
    document.removeEventListener('mousemove', this.handleCssResize);
    document.removeEventListener('mouseup', this.handleCssResizeEnd);
  };

  handleNameSubmit = event => {
    event.preventDefault();
    this.handleSave();
    this.nameInput.blur();
  };

  renderHeader() {
    const {resultOnly} = this.props;
    const {readOnly, savedLastRun} = this.state;
    return (
      <div className="App-header">
        {resultOnly
          ? <span className="NameTitle">
              {this.state.name}
            </span>
          : <form onSubmit={this.handleNameSubmit} className="NameForm">
              <input
                className="NameInput mousetrap"
                placeholder="Name this Stribble..."
                readOnly={readOnly}
                onChange={this.handleInputChange('name')}
                value={this.state.name}
                ref={input => (this.nameInput = input)}
              />
            </form>}
        {readOnly && <span className="Badge">Read-only</span>}
        {!readOnly &&
          !resultOnly &&
          <button className="HeaderAction" onClick={this.handleRunClick}>
            Run
          </button>}
        {!readOnly &&
          !resultOnly &&
          <button className="HeaderAction" onClick={this.handleSave}>
            Save
          </button>}
        {this.state.id &&
          <button className="HeaderAction" onClick={this.handleFork}>
            Fork
          </button>}
        <a
          className={`HeaderLink HeaderLink--code ${resultOnly ? '' : 'is-selected'}`}
          href={`/${this.state.id || ''}`}
        >
          Code
        </a>
        {this.state.id &&
          <a
            className={`HeaderLink ${resultOnly ? 'is-selected' : ''}`}
            href={`/${this.state.id}/result`}
          >
            Result
          </a>}
        {!savedLastRun &&
          <span className="Badge Badge--unsaved">Unsaved changes</span>}
        {this.state.updated_at &&
          <span className="UpdatedAtLabel">
            Last saved{' '}
            <TimeAgo
              date={this.state.updated_at}
              minPeriod={15}
              formatter={timeAgoFormatter}
            />
          </span>}
      </div>
    );
  }

  renderEditors() {
    const {jsCode, htmlCode, cssCode} = this.state;

    if (this.props.resultOnly) {
      return;
    } else {
      return (
        <div
          className="CodeContainer"
          style={{width: `${this.state.codeContainerWidth}%`}}
        >
          <div
            className="CodeSection"
            style={{flexBasis: `${this.state.jsContainerHeight}%`}}
          >
            <div className="EditorLabel">
              JavaScript
              <img
                className="EditorLabel-icon"
                src={optionsIcon}
                alt="options icon"
                onClick={() =>
                  this.setState({
                    showJsSettings: !this.state.showJsSettings,
                  })}
              />
              {this.state.showJsSettings
                ? <div className="EditorLabel-settingsContainer">
                    <label>Load Type</label>
                    <select
                      value={this.state.jsLoadType}
                      onChange={e => {
                        this.setState({jsLoadType: e.target.value});
                      }}
                    >
                      <option value="body">body</option>
                      <option value="head">head</option>
                      <option value="onLoad">onLoad</option>
                      <option value="onDomReady">onDomReady</option>
                    </select>
                  </div>
                : null}
            </div>
            <div className="EditorContainer">
              <MonacoEditor
                language="javascript"
                value={jsCode}
                onChange={this.handleChange('jsCode')}
                options={{...MONACO_OPTIONS, readOnly: this.state.readOnly}}
                theme="stribble"
                editorDidMount={this.handleEditorMount('javascript')}
              />
              {this.state.jsError
                ? <div className="Error">
                    {this.state.jsError}
                  </div>
                : null}
            </div>
          </div>
          <div
            className="CodeSection"
            style={{flexBasis: `${this.state.cssContainerHeight}%`}}
          >
            <div
              className="EditorLabel resizeable"
              tabIndex="0"
              onMouseDown={e => {
                this.offsetY = e.nativeEvent.offsetY;
                document.addEventListener('mousemove', this.handleJsResize);
                document.addEventListener('mouseup', this.handleJsResizeEnd);
              }}
            >
              CSS
            </div>
            <div className="EditorContainer">
              <MonacoEditor
                language="css"
                value={cssCode}
                onChange={this.handleChange('cssCode')}
                options={{...MONACO_OPTIONS, readOnly: this.state.readOnly}}
                theme="stribble"
                editorDidMount={this.handleEditorMount('css')}
              />
            </div>
          </div>
          <div
            className="CodeSection"
            style={{
              flexBasis: `${100 - this.state.jsContainerHeight - this.state.cssContainerHeight}%`,
            }}
          >
            <div
              className="EditorLabel resizeable"
              tabIndex="0"
              onMouseDown={e => {
                this.offsetY = e.nativeEvent.offsetY;
                document.addEventListener('mousemove', this.handleCssResize);
                document.addEventListener('mouseup', this.handleCssResizeEnd);
              }}
            >
              HTML
            </div>
            <div className="EditorContainer">
              <MonacoEditor
                language="html"
                value={htmlCode}
                onChange={this.handleChange('htmlCode')}
                options={MONACO_OPTIONS}
                editorDidMount={this.handleEditorMount('html')}
                theme="stribble"
              />
            </div>
          </div>
          <div
            className="CodeContainer-divider"
            tabIndex="0"
            onMouseDown={e => {
              this.setState({resizing: true});
              this.offsetX = e.nativeEvent.offsetX;
              document.addEventListener(
                'mousemove',
                this.handleHorizontalResize
              );
              document.addEventListener(
                'mouseup',
                this.handleHorizontalResizeEnd
              );
            }}
          />
        </div>
      );
    }
  }

  render() {

    const {transpiledJsCode, runHtmlCode, runCssCode, error, jsLoadType} = this.state;
    const outputClass = classNames('OutputContainer', {
      // The document.on('mousemove') listener stops working when the mouse is moved
      // over the iframe. To mitigate this we have to add `pointer-events: none` to
      // the iframe. To prevent any interference with the iframe we only apply this
      // class while resizing.
      'u-pointerEventsNone': this.state.resizing,
    });
    if (error) {
      return <NotFound />;
    } else {
      return (
        <div className={`App ${this.props.resultOnly ? 'App--resultOnly' : ''}`}>
          {this.renderHeader()}
          <div className="App-content" ref={c => (this.codeContainer = c)}>
            {this.renderEditors()}
            <div className={outputClass}>
              <Output
                javascript={transpiledJsCode}
                html={runHtmlCode}
                css={runCssCode}
                loadType={jsLoadType}
              />
            </div>
          </div>
        </div>
      );
    }
  }
}

export default App;



