const api = (resource, method, body) => {
  const options = {
    method: method || 'GET',
    headers: {},
  };

  if (body) {
    options.body = JSON.stringify(body);
    options.headers['Content-Type'] = 'application/json';
  }

  return fetch(`https://stribble.herokuapp.com/${resource}`, options)
    .then(response => response.json())
    .then(response => {
      if (response.error && response.status === 404) {
        return response;
      } else if (response.error) {
        throw new Error(response.error);
      } else {
        return response;
      }
    });
};

export const getStribbles = () => api('stribbles.json');
export const getStribble = id => api(`stribbles/${id}.json`);
export const createStribble = data =>
  api('stribbles.json', 'POST', {stribble: data});
export const updateStribble = (id, data) =>
  api(`stribbles/${id}.json`, 'PUT', {stribble: data});
export const forkStribble = id => api(`stribbles/${id}/fork.json`, 'POST');



